
* { -webkit-box-sizing: border-box; box-sizing: border-box;
}
body, html { 
  height: 100%; 
  margin: 0;
  padding: 0;
}
body {
  background: url(/static/img/feast.97dcdf5.jpg) no-repeat center center;
  background-size: cover;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  background: rgba(0,0,0,0.3);
  color: #FFF;
  height: 100vh;
}

@charset "UTF-8";
main {
  height: 100%;
}
.brand {
  position: fixed;
  bottom: 10px;
  right: 20px;
  text-align: right;
}
.brand img {
    margin-right: 10px;
    max-width: 48px;
    vertical-align: middle;
}
.graph {
  background: rgba(0, 0, 0, 0.5);
  height: 33vh;
  position: relative;
}
.graph .chart {
    height: 100%;
}
.rdata {
  height: 67vh;
  padding: 20px;
}
.milestones {
  float: right;
  position: relative;
  -webkit-transform: translateY(-16px);
          transform: translateY(-16px);
  width: calc(100% - 44px);
}
.milestones .pog {
    background: black;
    border-radius: 100%;
    -webkit-box-shadow: 0 0 0 3px white;
            box-shadow: 0 0 0 3px white;
    display: inline-block;
    line-height: 32px;
    position: absolute;
    top: 0;
    -webkit-transform: translateX(-24px);
            transform: translateX(-24px);
    width: 32px;
    /*
    &:after {
      background: white;
      content: '';
      height: 33vh;
      position: absolute;
      left: 50%; bottom: 32px;
      width: 1px;
      z-index: -1;
    }
    */
}
.milestones .pog:before {
      background-size: 100% auto !important;
      border-radius: 100%;
      content: '';
      display: block;
      height: 32px;
      width: 32px;
}
.milestones .pog[data-type="firstKill"]:before {
      content: "\1F5E1";
}
.milestones .pog[data-type="expand"]:before {
      background: url(data:image/png;base64,) no-repeat center center;
      display: block;
}
.milestones .pog[data-type="t3Kill"]:before {
      content: "\2620";
}
.milestones .pog[data-type="t2Build"]:before {
      background: url(data:image/png;base64,) no-repeat center center;
}
.milestones .pog[data-type="t3Build"]:before {
      background: url(data:image/png;base64,) no-repeat center center;
}
.milestones .pog[data-faction="0"] {
      -webkit-box-shadow: 0 0 0 3px #D21E1E;
              box-shadow: 0 0 0 3px #D21E1E;
}
.milestones .pog[data-faction="1"] {
      -webkit-box-shadow: 0 0 0 3px #148CFA;
              box-shadow: 0 0 0 3px #148CFA;
}
.milestones .pog[data-faction="2"] {
      -webkit-box-shadow: 0 0 0 3px #FFDC00;
              box-shadow: 0 0 0 3px #FFDC00;
}
.milestones .pog[data-faction="3"] {
      -webkit-box-shadow: 0 0 0 3px #2ECC40;
              box-shadow: 0 0 0 3px #2ECC40;
}
.dzone {
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background: rgba(0, 0, 0, 0.5);
  border: 3px dashed white;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 50vh;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  position: absolute;
  top: 25vh;
  left: 10vw;
  width: 80vw;
}
.replay + .dzone {
    height: 64px;
    left: 20px;
    width: 200px;
    top: auto;
    bottom: 20px;
}

@-webkit-keyframes fadeUp {
from {
    opacity: 0;
    -webkit-transform: translateY(30px);
            transform: translateY(30px);
}
to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
}
@keyframes fadeUp {
from {
    opacity: 0;
    -webkit-transform: translateY(30px);
            transform: translateY(30px);
}
to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
}
.player {
  -webkit-animation: fadeUp 1s ease;
          animation: fadeUp 1s ease;
  display: inline-block;
  margin: 0 10px;
  min-width: 352px;
  padding: 0 20px;
  position: relative;
  width: 22%;
  vertical-align: top;
}
.player:nth-child(2) {
    -webkit-animation: fadeUp 2s ease;
            animation: fadeUp 2s ease;
}
.player .portrait {
    background: url(/static/img/portrait_block.5aa7214.png) no-repeat center top;
    background-size: 100% auto;
    height: 345px;
    margin: 0;
    max-width: 376px;
    padding: 15% 6% 4%;
    position: relative;
    width: 100%;
}
.player .portrait h2 {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
}
.player .portrait img {
      dispaly: block;
      width: 100%;
}
.player[data-faction="Hopper"] h2 {
    color: #D21E1E;
}
.player[data-faction="Bellafide"] h2 {
    color: #148CFA;
}
.player[data-faction="Archimedes"] h2 {
    color: #FFDC00;
}
.player[data-faction="Quartermaster"] h2 {
    color: #2ECC40;
}
.player .deck {
    position: absolute;
    top: 270px;
    left: 0;
    width: 100%;
}
.player .deck .pog {
      -webkit-filter: drop-shadow(0 0 10px black);
              filter: drop-shadow(0 0 10px black);
      max-width: 48px;
}
.player .actions {
    list-style: none;
    margin: 0;
    max-height: 160px;
    overflow: auto;
    padding: 0;
    text-align: left;
    vertical-align: top;
}
.player .actions li {
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      background: rgba(0, 0, 0, 0.5);
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      margin-bottom: 2px;
      padding: 5px 10px;
      text-transform: capitalize;
}
.player .actions li span {
        -webkit-box-flex: 1;
            -ms-flex-positive: 1;
                flex-grow: 1;
}
.player .actions li img {
        max-width: 32px;
}
